.titulo {
	font-family: 'Montserrat', sans-serif;
	font-size: 70px;
	margin-bottom: 0;
	margin-top: 70px;
}

.titulo span {
	text-decoration: underline;
}

.title-a {
	font-size: 24px;
	margin-top: 0;
}

.title-b {
	font-size: 50px;
	margin-bottom: 20px;
}

.guitarras {
	color: #1f313c;
	margin-bottom: 60px;
}

.guitarras h2 {
	font-family: 'Montserrat', sans-serif;
	font-size: 35px;
}

.portada {
	color: white;
	padding: 20px;
	height: 500px;
}

.portada button {
	margin-bottom: 70px;
}

.menu {
	font-size: 24px;
	display: inline-block;
	position: absolute;
	right: 0;
}

.menu li {
	display: inline-block;
	margin-left: 30px;
}

.menu li:before {
	content: '∘';
	font-size: 50px;
	line-height: 20px;
	color: white;
}

.menu a {
	color: white;
	text-decoration: none;
}

.logotipo {
	display: inline-block;
	margin-left: 0;
}

.guitarra {
	position: relative;
	margin: 10px 0 40px;
	padding: 10px 2em;
	/*overflow: hidden;*/
	border: 1px solid #1f313c;
	border-radius: 5px;

	display: flex;
	align-items: center;
	min-height: 250px;
	transition: 0.3s;
}
.guitarra:hover {
	box-shadow: 10px 10px 0 #fe344e;
}
.guitarra:nth-of-type(even):hover {
	box-shadow: -10px 10px 0 #ff7250;
}
.guitarra-name {
	font-size: 3em;
	margin: 0 0 0.2em;
	padding-bottom: 0.2em;
	border-bottom: 2px dashed #1e313d;
}
/* .guitarra:nth-of-type(1) {
	background: red;
} */
.guitarra:nth-of-type(even) {
	justify-content: flex-end;
}
.guitarra-image {
	position: absolute;
	right: 0;
	bottom: 0;
}
.guitarra:nth-of-type(even) .guitarra-image {
	left: 0;
	right: initial;
}
.guitarra ol {
	padding: 0;
	list-style: none;
	counter-reset: features;
}
.guitarra li::before {
	counter-increment: features;
	content: counter(features) '. ';
}
